<template>
  <div>
      <div class="box">
          <Head>
              <router-link to="/List" slot="left">
                  <img src="../../images/110.png" alt="">
              </router-link>
              <h4 slot="center">{{lost.name}}</h4>
              <span slot="right">
                  <router-link to="/List">
                      切换城市
                  </router-link>
              </span>
          </Head>
          <div class="content">
              <div class="inp">
                  <input type="text" placeholder="输入学校，商务楼，地址" v-model="val">
                  <button @click="sub">提交</button>
              </div>
              <div class="ser" @click="placehistory">
                  搜索历史
              </div>
              <div class="zhong">
                  <span v-for="item in info" :key="item.id" @clcik="subNext(item)" v-show="isShow">
                      <router-link class="a" :to="{name:'Goods',params:{name:item.name,geohash:item.geohash}}">
                         {{item.name}}
                          </router-link>
                      <P>
                         {{item.address}}
                      </P>
                  </span>
                  <!-- <hr /> -->
                    <span v-for="(item,index) in searchlist" :key="index" @click="subNext(item)">
                        <router-link class="a" :to="{name:'Goods',params:{name:item.name,geohash:item.geohash}}">
                             {{item.name}}
                         </router-link>
                        <P>
                            {{item.address}}
                        </P>
                    </span>
                  <hr />
                  <span class="del" @click="del">清空所有</span>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
import Head from  '../Head/Header.vue'
import axios from 'axios'
export default {
    components:{
        Head
    },
    data(){
        return {
            lost:'',
            val:'',
            searchlist:[],
            info:[],
            isShow:false
        }
    },
    mounted(){
        function  rem(){
		document.documentElement.style.fontSize=document.documentElement.clientWidth/7.5+"px";
	}
	//onresize  当屏幕的尺寸发生改变的时候会触发的事件
	rem();
  window.onresize=rem;
         axios.get('http://elm.cangdu.org/v1/cities/'+this.$route.params.id).then(res=>{          
            //   console.log(res)
            this.lost = res.data;
         })
     },
     methods:{
         sub(){
             if(this.val){
                 axios.get('http://elm.cangdu.org/v1/pois?type=search&city_id='+this.$route.params.id+'&keyword='+this.val)
                 .then(res=>{
                     this.searchlist=res.data
                 })
             }
         },
         subNext(item){
             let check = false ; 
             this.info.forEach(i => {
                 if(i.address == item.address){
                     check=true
                 }
             });
             if(check==false){
                 this.info.push(item)
             }
             localStorage.setItem('history',JSON.stringify(this.info))
            //  this.$router.push({name:'Goods',params:{name:item.name}})
         },
         del(){
             this.info = []
             localStorage.removeItem('history',null)
         },
         placehistory(){
             this.isShow = true;
         }
     },
     created(){
         if(localStorage.getItem('history')){
             this.info = JSON.parse(localStorage.getItem('history'))
         }
     }
}
</script>

<style lang="less" scoped>
    .box{
      width: 100%;
      header{
        width: 100%;
        height: 0.8rem;
        background: #3792E5;
        position: relative;
        img{
            position: absolute;
            top: 0.2rem;
            left: 0.2rem;
            width: 0.3rem;
        }
        h4{
            position: absolute;
            color: white;
            top: -0.4rem;
            font-size: 0.4rem;
            left: 3.2rem;
        }
        a{
            color: white;
            text-decoration: none;
        }
        span{
            display: block;
            position: absolute;
            top: 0.2rem;
            right: 0.2rem;
            color: white;
            font-size: 0.35rem;
        }
      }
      .content{
          width: 100%;
          .inp{
             width: 100%;
             input{
                 width: 6.75rem;
                 height: 0.65rem;
                 margin-left: 0.3rem;
                 margin-top: 0.3rem;
                 text-indent: 0.2rem;
                 font-size: 0.35rem;
                 border: 1px solid black;
              }
            button{
                width: 6.85rem;
                height: 0.7rem;
                margin-left: 0.3rem;
                background: #3792E5;
                color: white;
                font-size: 0.4rem;
                border: none;
                margin-top: -1.5rem;
            }
         }
         .ser{
             margin-top: 0.2rem;
             text-indent: 0.4rem;
             width: 100%;
             height: 0.4rem;
             background: #F5F5F5;
             border: 0.04rem solid #E7E7E7;
             border-left: none;
             border-right: none;
             font-size: 0.24rem;
             line-height: 0.4rem;
         }
         .zhong{
                width: 100%;
                .a{
                    text-decoration: none;
                    color: black;
                }
                span{
                    display: block;
                    font-size: 0.4rem;
                    font-weight: 530;
                    margin-left: 0.3rem;
                    margin-top: 0.4rem;
                }
                p{
                    display: block;
                    font-size: 0.3rem;
                    // margin-left: 1.9rem;
                    margin-top: 0.4rem;
                }
                hr{
                    margin-top: 0.6rem;
                    color: #E4E4E4;
                }
                .del{
                    display: block;
                    margin-left: 3rem;
                }
            }
            
      }
    }
</style>


